@charset "UTF-8";
/*-----------------------------
* 基本ブロック（継承して利用）
-----------------------------*/
// 両方設定
%ext_base_block {
	position: relative;
	@include desktop {
		width: $contentBaseWidgth_PC;
		margin-left: auto;
		margin-right: auto;
	}
	@include smartphone {
		width: $spBaseWidth;
		margin: 0 auto;
	}
}

// PCだけ設定
%ext_base_block_pc {
	@include desktop {
		position: relative;
		width: $contentBaseWidgth_PC;
		margin-left: auto;
		margin-right: auto;
	}
}

// スマホだけ設定
%ext_base_block_sp {
	@include smartphone {
		position: relative;
		margin-left: 20px;
		margin-right: 20px;
	}
}


/** ---------------------
 * 要素ごとのCSS
 * ------------------- */
// PCのみ表示
.only_pc {
	@include smartphone {
		display: none;
	}
}

// SPのみ表示
.only_sp {
	display: none;
	@include smartphone {
		display: inherit;
	}
}

// ボタンのロールオーバー
.btnImg_hover {
	@include desktop {
		transition: all 0.3s $ease-in-out;
		&:hover {
			opacity: 0.6;
		}
	}
}

// 文字を小さくする ※Chromeで10px以下にならないため、transformで制御する
.subsmall {
	display: inline-block;
	text-indent: 0;
	-webkit-transform: scale(0.8);
	-ms-transform: scale(0.8);
	transform: scale(0.8);
	-webkit-transform-origin: 25% 70%;
	-ms-transform-origin: 25% 70%;
	transform-origin: 25% 70%;
}


/** ---------------------
 * 全体共通CSS
 * ------------------- */
body {
	background-color: $baseBkgColor;
	color: $base-font-color;
	font-family: "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, sans-serif;
	& > img {
		// 計測タグが生成するimgタグの影響で余白が出てしまうため
		position: absolute;
		@include smartphone {
			max-width: 100%;
	        height: auto;
		}
	}
	@include desktop {
		font-size: $base-font-size;
		min-width: $contentBaseWidgth_PC;
		background: url(#{$imgPath}bg_overall.jpg) no-repeat 50% 0;
		background-size: 100% auto;
	}
	@include smartphone {
		min-width: inherit;
		-webkit-text-size-adjust: 100%;
	}
}

// aタグのリンク文字
a,
a:link, // 未訪問のリンク
a:visited,
a:hover,
a:active { // クリックしてから離されるまでの状態
	color: $linkColor;
}


/** ---------------------
 * コンテンツエリアCSS
 * ------------------- */
// メインビジュアル
.mv_area {
	.mv_contents {
		@extend %ext_base_block;
		@include desktop {
			position: relative;
			margin: 0 auto;
			padding-top: 23px;
		}
		@include smartphone {
			padding-top: 20px;
		}
		h1 {
			@include desktop {
				position: absolute;
				top: 23px;
				right: -13px;
			}
		}
		.mv_copy {
			@include desktop {
				position: absolute;
				top: 296px;
				@include backgroundFig(774px, 153px, "mv_copy.png");
			}
		}
		.mv_thumb {
			@include desktop {
				position: absolute;
				top: 36px;
				@include backgroundFig(322px, 240px, "mv_thumb.png");
			}
		}
		.mv_item {
			@include desktop {
				position: absolute;
				top: 297px;
				right: 0;
				@include backgroundFig(164px, 223px, "mv_item.png");
			}
		}
		.mv_sns {
			display: flex;
			justify-content: space-between;
			position: relative;
			@include desktop {
				width: 110px;
				float: right;
			}
		}
	}
}

// リード
.lead_area {
	clear: right;
	@include desktop {
		@include clearfix;
		margin-top: 445px;
		background: url(#{$imgPath}lead_bg.gif);
	}
	.lead_contents {
		@extend %ext_base_block;
		@include clearfix;
		@include desktop {
			padding: 9px 0;
		}
	}
	.lead_txt {
		@include desktop {
			float: right;
			width: 556px;
			font-size: 18px;
			line-height: 2.33em;
			padding-top: 48px;
		}
	}
	.lead_img {
		@include desktop {
			float: left;
		}
	}
	&:before {
		content: "";
		display: block;
		height: 15px;
		background: url(#{$imgPath}lead_bg_top.gif) repeat-x;
	}
	&:after {
		content: "";
		display: block;
		height: 15px;
		background: url(#{$imgPath}lead_bg_bottom.gif) repeat-x;
	}
}


/** ---------------------
 * フッターCSS
 * ------------------- */
footer {
	background-color: #fff;
	.footer_contens {
		@extend %ext_base_block;
		@include desktop {
			margin: 0 auto;
			padding: 20px 0;
		}
		@include smartphone {
			padding: 20px 0;
		}
		.copyright {
			text-align: center;
			@include desktop {
				font-family: "Quicksand", "游ゴシック", "Yu Gothic", YuGothic, "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, sans-serif;
				font-size: 10px;
				line-height: 1em;
			}
			@include smartphone {
				width: 100%;
				font-size: 8px;
			}
		}
	}
}
